import { useFormik } from 'formik'
import * as yup from 'yup'
import React, { useState } from 'react'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'


export default function Register() {
  let [error,seterror]=useState("")
  let [loading,setloading]=useState(false)
  let navg=useNavigate()
let validationSchema=yup.object({
  name:yup.string().required("name required").min(3,"min char 3").max(20,"max char 20"),
  email:yup.string().required("email required").email("enter valid email"),
  password:yup.string().required("Password required").matches(/^[a-zA-Z!@#$%^&_0-9]{6,16}$/,"enter valid Password"),
  rePassword:yup.string().required("rePassword required").oneOf([yup.ref("password")]),
  phone:yup.string().required("phone required").matches(/^01[1250][0-9]{8}$/,"enter valid phone")
})
async function  Register(val){
  setloading(true)
let req= await axios.post('https://ecommerce.routemisr.com/api/v1/auth/signup',val).catch(function(error){
  seterror(error.response.data.message)
  setloading(false)
})
if((req?.data.message)=='success'){
  navg('/login')
  setloading(false)
}
}
 let formik=useFormik({
 initialValues:{name:"",
  email:"",
  password:"",
  rePassword:"",
  phone:"",
 },
 onSubmit:Register
 ,
 validationSchema
}
 )
  return (
    <div className='container mt-5 pt-5'>
      <h2>Register Now</h2>
      {error!=""?<div className='alert alert-danger'>{error}</div>:""}
     <form onSubmit={formik.handleSubmit}>
      <label htmlFor='name'>name</label>
      <input onBlur={formik.handleBlur} onChange={formik.handleChange} type='text' className='form-control' id='name' name='name'/>
     {( formik.errors.name&&formik.touched.name)?<div className='alert alert-danger mt-3'>{formik.errors.name}</div>:""}
      <label htmlFor='email'>email</label>
      <input onBlur={formik.handleBlur} onChange={formik.handleChange} type='email' className='form-control' id='email' name='email'/>
      {( formik.errors.email&&formik.touched.email)?<div className='alert alert-danger mt-3'>{formik.errors.email}</div>:""}
      <label htmlFor='password'>password</label>
      <input onBlur={formik.handleBlur} onChange={formik.handleChange} type='password' className='form-control' id='password' name='password'/>
      {( formik.errors.password&&formik.touched.password)?<div className='alert alert-danger mt-3'>{formik.errors.password}</div>:""}
      <label htmlFor='rePassword'>rePassword</label>
      <input onBlur={formik.handleBlur} onChange={formik.handleChange} type='password' className='form-control' id='rePassword' name='rePassword'/> 
      {( formik.errors.rePassword&&formik.touched.rePassword)?<div className='alert alert-danger mt-3'>{formik.errors.rePassword}</div>:""}
      <label htmlFor='phone'>phone</label>
      <input onBlur={formik.handleBlur} onChange={formik.handleChange} type='tel' className='form-control' id='phone' name='phone'/> 
      {( formik.errors.phone&&formik.touched.phone)?<div className='alert alert-danger mt-3'>{formik.errors.phone}</div>:""}
      {
        loading?<button className='btn btn-success mt-3'><i className='fa-solid fa-spinner fa-spin'></i></button>:<button disabled={!(formik.isValid&&formik.dirty)} type='submit' className='btn btn-success mt-3'>Register</button>
      }
     </form>
    </div>
  )
}
