import axios from "axios";
import { jwtDecode } from "jwt-decode";
import { createContext, useEffect, useState } from "react";


export let CartContext=createContext();
export default function CartContextProvider({children}){
    let[cartCount,setCartCount]=useState(0)
    function addToCart(ProductID){
        let body={
            productId:ProductID
        }
        return axios.post('https://ecommerce.routemisr.com/api/v1/cart',body,{headers:{
            token:localStorage.getItem("user")
        }})
    }
    function GetCarts(){
        return axios.get(`https://ecommerce.routemisr.com/api/v1/cart`,{headers:{
            token:localStorage.getItem("user")
        }})
    }
    function DeleteCartItem(ProductID){
       
        return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${ProductID}`,{headers:{
            token:localStorage.getItem("user")
        }})
    }
    function UpdateCartItem(ProductID,count){
        let body={
            count:count
        }
        return axios.put(`https://ecommerce.routemisr.com/api/v1/cart/${ProductID}`,body,{headers:{
            token:localStorage.getItem("user")
        }})
    }
    function CheckOut(id,data){
    let body=data   
    let header= {headers:{
        token:localStorage.getItem("user")
    }}
     return axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${id}?url=http://localhost:3000`,body,header)
    }
    function GetOrders(id){
        return axios.get(`https://ecommerce.routemisr.com/api/v1/orders/user/${id}`)
    }
    return <CartContext.Provider value={{GetOrders,CheckOut,GetCarts,addToCart,setCartCount,cartCount,DeleteCartItem,UpdateCartItem}}>
       {children}
    </CartContext.Provider>
}