import axios from "axios";
import { useFormik } from "formik";
import React ,{ useContext, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import * as yup from "yup";
import { UserContext } from './../../Context/UserContext';

export default function ResetPassword() {
  let [error,seterror]=useState("")
  let [loading,setloading]=useState(false)
  let navg=useNavigate()
  let validationSchema = yup.object({
    email: yup.string().required("email required").email("enter valid email"),
    newPassword: yup
      .string()
      .required("Password required")
      .matches(/^[A-Z][a-zA-Z!@#$%^&_0-9]{6,16}$/, "enter valid Password"),
  });
  async function ResetPassword(val) {
    let data=await axios.put('https://ecommerce.routemisr.com/api/v1/auth/resetPassword',val).catch((err)=>{
        seterror(err.response.data.message)
    })
    if(data?.data.token){
        navg('/login')
    }
  }
  let formik = useFormik({
    initialValues: {
      email: "",
      newPassword: "",
    },
    onSubmit:ResetPassword,
    validationSchema,
  });
  return (
    <div className="container mt-5 pt-5">
      <h2>Reset Password</h2>
      {error?<div className="alert alert-danger">{error}</div>:""}
      <form onSubmit={formik.handleSubmit}>
        <label htmlFor="email">email</label>
        <input
          onBlur={formik.handleBlur}
          name="email"
          onChange={formik.handleChange}
          className="form-control"
          type="email"
          id="email"
        />
        {formik.errors.email && formik.touched.email ? (
          <div className="alert alert-danger">{formik.errors.email}</div>
        ) : (
          ""
        )}
        <label className="mt-2" htmlFor="password">
        newPassword
        </label>
        <input
          onBlur={formik.handleBlur}
          name="newPassword"
          onChange={formik.handleChange}
          className="form-control"
          type="password"
          id="newPassword"
        />
        {formik.errors.newPassword && formik.touched.newPassword ? (
          <div className="alert alert-danger">{formik.errors.newPassword}</div>
        ) : (
          ""
        )}
        {!loading?<button type="submit" className="btn btn-success mt-2">
          Update password
        </button>:<button className='btn btn-success mt-3'><i className='fa-solid fa-spinner fa-spin'></i></button>}
      </form>
    </div>
  );
}
