import React, { useContext, useEffect ,useState} from "react";
import { Link } from "react-router-dom";
import { CartContext } from "../../Context/CartContext";
import Swal from "sweetalert2";
import { WishlistContext } from "../../Context/WishlistContext";

export default function Product({ item ,removeWishlist}) {
  let { addToCart, setCartCount } = useContext(CartContext);
  let [redHeart,setRedHeart]=useState(false)
  let { addToWishlist,removeToWishlist,getWishlist,wishList,setWishList} = useContext(WishlistContext);
  
  async function addCart(id) {
    let req = await addToCart(id);
    if (req.data.status == "success") {
      setCartCount(req.data.numOfCartItems);
      Swal.fire({
        title: "Good job!",
        text: "You clicked the button!",
        icon: "success",
      });
    }
  }
  async function addWishlist(id) {
    let req = await addToWishlist(id).catch();
    if(req.data.status=='success'){
      Swal.fire({
        title: "Good job!",
        text: "You clicked the button!",
        icon: "success",
      });
    }
  }
 
  return (
    <div className="col-md-2">
      <div className="product d-flex flex-column align-content-center p-2 rounded">
        <Link to={"/productDetails/" + item.id}>
          <img src={item.imageCover} className="w-100" />
          <div className="p-2">
            <p className="text-main">{item.category.name}</p>
            <p>{item.title.split(" ").slice(0, 2).join(" ")}</p>
            <div className="d-flex justify-content-between">
              <p>{item.price}EGP</p>
              <p>
                {item.ratingsQuantity}{" "}
                <i className="fa-solid fa-star rating-color"></i>
              </p>
            </div>
          </div>
        </Link>
      {(redHeart==true || wishList?.data?.data.includes(item)==true) ?<i
          onClick={() => {
            removeWishlist(item.id);
            setRedHeart(false)
          }}
          className="fa-solid fa-heart fa-xl text-danger cursor-pointer p-3"
        ></i>: <i
          onClick={() => {
            addWishlist(item.id);
            setRedHeart(true)
          }}
          className="fa-solid fa-heart fa-xl cursor-pointer p-3"
        ></i>}
        <button
          className="btn bg-main my-2 text-light"
          onClick={() => {
            addCart(item.id);
          }}
        >
          Add to cart
        </button>
      </div>
    </div>
  );
        }