import axios from "axios";
import { useFormik } from "formik";
import React ,{ useContext, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import * as yup from "yup";
import { UserContext } from './../../Context/UserContext';
import { CartContext } from "../../Context/CartContext";

export default function Login() {
  let {setUsertoken}=useContext(UserContext)
  let [error,seterror]=useState("")
  let [loading,setloading]=useState(false)
  let navg=useNavigate()
  let{GetCarts,setCartCount}=useContext(CartContext)
  let validationSchema = yup.object({
    email: yup.string().required("email required").email("enter valid email"),
    password: yup
      .string()
      .required("Password required")
      .matches(/^[a-zA-Z!@#$%^&_0-9]{6,16}$/, "enter valid Password"),
  });
  async function getCartCount(){
    let req=await GetCarts();
    if(req?.data.status=='success'){
      setCartCount(req?.data?.numOfCartItems)
    }
  }
  async function signIn(val) {
    setloading(true)
    let req = await axios.post(
      "https://ecommerce.routemisr.com/api/v1/auth/signin",val).catch((er)=>{
        seterror(er.response.data.message)
        setloading(false)
    })
    if(req?.data.message==='success'){
      localStorage.setItem("user",req.data.token)
      setUsertoken(req?.data.token)
      navg('/')
      setloading(false)
      getCartCount()
    }
  
  }
  let formik = useFormik({
    initialValues: {
      email: "",
      password: "",
    },
    onSubmit: (val) => {
      signIn(val);
    },
    validationSchema,
  });
  return (
    <div className="container mt-5 pt-5">
      <h2>Login</h2>
      {error?<div className="alert alert-danger">{error}</div>:""}
      <form onSubmit={formik.handleSubmit}>
        <label htmlFor="email">email</label>
        <input
          onBlur={formik.handleBlur}
          name="email"
          onChange={formik.handleChange}
          className="form-control"
          type="email"
          id="email"
        />
        {formik.errors.email && formik.touched.email ? (
          <div className="alert alert-danger">{formik.errors.email}</div>
        ) : (
          ""
        )}
        <label className="mt-2" htmlFor="password">
          password
        </label>
        <input
          onBlur={formik.handleBlur}
          name="password"
          onChange={formik.handleChange}
          className="form-control"
          type="password"
          id="password"
        />
        {formik.errors.password && formik.touched.password ? (
          <div className="alert alert-danger">{formik.errors.password}</div>
        ) : (
          ""
        )}
        {!loading?<button type="submit" className="btn btn-success mt-2">
          Login
        </button>:<button className='btn btn-success mt-3'><i className='fa-solid fa-spinner fa-spin'></i></button>}
        <Link to='/forgetPassword' className="text-decoration mt-3 p-3 cursor-pointer">Forget password</Link>
      </form>
    </div>
  );
}
