import axios from 'axios'
import React, { useEffect, useState } from 'react'
import Product from '../Product/Product'
import { useQuery } from 'react-query'

export default function Products() {
  function getProducts(){
    return axios.get("https://ecommerce.routemisr.com/api/v1/products")
  }
let{isLoading,data}=useQuery("getAllProducts",getProducts)

  return (
    isLoading?<div className='d-flex justify-content-center align-items-center vh-100 position-fixed w-100 top-0 bg-light'><span className="loader"></span></div>:
    <div className='container mt-5 pt-5'>
      <div className='row g-3'>
      {data?.data.data.map((item)=>{
     return <Product key={item.id} item={item}></Product>
      })}
      </div>
     </div>
  )
}
