import logo from './logo.svg';
import './App.css';
import { RouterProvider, createHashRouter } from 'react-router-dom';
import Layout from './Components/Layout/Layout';
import Register from './Components/Register/Register';
import Login from './Components/Login/Login';
import Products from './Components/Products/Products';
import Home from './Components/Home/Home';
import UserContextProvider from './Context/UserContext';
import CartContextProvider from './Context/CartContext';
import Cart from './Components/Cart/Cart';
import Brands from './Components/Brands/Brands';
import ProtectRoute from './Components/ProtectedRoute/ProtectedRoute';
import Notfound from './Components/Notfound/Notfound';
import ForgetPassword from './Components/ForgetPassword/ForgetPassword';
import ResetPassword from './Components/ResetPassword/ResetPassword';
import { QueryClient, QueryClientProvider } from 'react-query'
import { ReactQueryDevtools } from 'react-query/devtools'
import ProductDetails from './Components/ProductDetails/ProductDetails';
import { ToastContainer } from 'react-toastify';
import Checkout from './Components/Checkout/Checkout';
import Allorders from './Components/Allorders/Allorders';
import Categories from './Components/Categories/Categories';
import WishlistContextProvider from './Context/WishlistContext';
import Wishlist from './Components/Wishlist/Wishlist';
let router=  createHashRouter([{
  path:"/",element:<Layout/>,children:[  
    {index:true,element:<ProtectRoute><Home/></ProtectRoute>},
      {path:"login",element:<Login/>},
      {path:"resetPassord",element:<ResetPassword/>},
      {path:"forgetPassword",element:<ForgetPassword/>},
      {path:"products",element:<ProtectRoute><Products/></ProtectRoute>},
      {path:"checkout/:id",element:<ProtectRoute><Checkout/></ProtectRoute>},
      {path:"allorders",element:<ProtectRoute><Allorders/></ProtectRoute>},
      {path:"cart",element:<ProtectRoute><Cart/></ProtectRoute>},
      {path:"categories",element:<ProtectRoute><Categories/></ProtectRoute>},
      {path:"wishlist",element:<ProtectRoute><Wishlist/></ProtectRoute>},
      {path:"brands",element:<ProtectRoute><Brands/></ProtectRoute>},
      {path:"productDetails/:id",element:<ProtectRoute><ProductDetails/></ProtectRoute>},
      {path:"register",element:<Register/>},
      {path:"*",element:<Notfound/>}
  ]
}])
function App() {
  let rQuery=new QueryClient();
  return <>
  <QueryClientProvider client={rQuery}>
    {/* <ReactQueryDevtools></ReactQueryDevtools> */}
  <CartContextProvider>
  <UserContextProvider>
  <WishlistContextProvider>
  <RouterProvider router={router}/>
  </WishlistContextProvider>
  </UserContextProvider>
  </CartContextProvider>
  </QueryClientProvider>
  <ToastContainer theme='colored'/>
  </>;
}

export default App;
