import React, { useContext } from "react";
import { Link, NavLink, useNavigate } from "react-router-dom";
import logo from "../../assets/images/freshcart-logo.53f7a424c3aedc30a0fb46dc2278137c.svg";
import { UserContext } from "../../Context/UserContext";
import { CartContext } from "../../Context/CartContext";
export default function NavBar() {
  let { userToken, setUsertoken, data } = useContext(UserContext);
  let { cartCount } = useContext(CartContext);
  let navg = useNavigate();
  function logOut() {
    localStorage.removeItem("user");
    setUsertoken(null);
    navg("/login");
  }
  return (
    <nav className="navbar navbar-expand-lg bg-body-tertiary w-100 position-fixed top-0 z-3 py-3">
      <div className="container-fluid">
        <Link className="navbar-brand" to="/">
          <img src={logo} />
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            {userToken !== null ? (
              <>
                {" "}
                <li className="nav-item">
                  <NavLink className="nav-link" aria-current="page" to="/">
                    Home
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="products">
                    Products
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="categories">
                    Categories
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="cart">
                    Cart
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="brands">
                    Brands
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="allorders">
                    All orders
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="wishlist">
                  Wishlist
                  <i className="fa-solid fa-heart fa-xl px-1"></i>
                  </NavLink>
                </li>
              </>
            ) : (
              ""
            )}
          </ul>
          <ul className="navbar-nav ms-auto mb-2 ">
            <li className="d-flex align-items-center">
              <i className="fa-brands fa-facebook mx-2"></i>
              <i className="fa-brands fa-twitter mx-2"></i>
              <i className="fa-brands fa-youtube mx-2"></i>
              <i className="fa-brands fa-instagram mx-2"></i>
              <i className="fa-brands fa-pinterest mx-2"></i>
            </li>
            {userToken !== null ? (
              <>
               <li className="nav-item d-flex align-items-center position-relative px-1">
               <Link to='/cart'>
                  <i className="fa-solid fa-cart-shopping fa-xl text-main cursor-pointer"></i>
                  <span className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger">
                  {cartCount}+
                  </span>
                  </Link>
                </li>
                <li className="nav-item d-flex">
                  <span className="nav-link cursor-pointer text-main">
                    {data?.name}
                  </span>
                  <span
                    onClick={logOut}
                    className="nav-link cursor-pointer text-danger fw-bold"
                  >
                    LogOut
                  </span>
                </li>
              </>
            ) : (
              <>
                <li className="nav-item">
                  <NavLink className="nav-link" to="register">
                    Register
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="login">
                    Login
                  </NavLink>
                </li>
              </>
            )}
          </ul>
        </div>
      </div>
    </nav>
  );
}
