import React from 'react'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import img1 from '../../assets/images/slider-image-1.jpeg'
import img2 from '../../assets/images/slider-image-2.jpeg'
import img3 from '../../assets/images/slider-image-3.jpeg'
import img4 from '../../assets/images/grocery-banner.png'
import img5 from '../../assets/images/grocery-banner-2.jpeg'
export default function Carousel() {
  return (
 <div className='row g-0'>
<div className='col-md-9'>
<OwlCarousel items={1} className='owl-theme' loop margin={10} autoplay={true} >
    <div className='item'>
        <img src={img1} className='w-100' height={400}/>
    </div>
    <div className='item'>
    <img src={img2} className='w-100' height={400}/>
    </div>
    <div className='item'>
    <img src={img3} className='w-100' height={400}/>
    </div>
    <div className='item'>
    <img src={img4} className='w-100' height={400}/>
    </div>
    <div className='item'>
    <img src={img5} className='w-100' height={400}/>
    </div>
</OwlCarousel>
</div>
<div className='col-md-3'>
<img src={img3} className='w-100' height={200}/>
<img src={img2} className='w-100' height={200}/>
</div>
 </div>
  )
}
