import axios from "axios";
import { createContext, useState } from "react";

export let WishlistContext=createContext()
export default function WishlistContextProvider({children}){
  let [wishList,setWishList]=useState({})
  let [redHeart,setRedHeart]=useState(true)
    function getWishlist(){
       
         return axios.get('https://ecommerce.routemisr.com/api/v1/wishlist',{headers:{
            token:localStorage.getItem("user")
        }})
     }
    function addToWishlist(id){
       let body={
            "productId": id,
        }
        let header= {headers:{
            token:localStorage.getItem("user")
        }}
        return axios.post('https://ecommerce.routemisr.com/api/v1/wishlist',body,header)
    }
    function removeToWishlist(id){
        let header= {headers:{
            token:localStorage.getItem("user")
        }}
        return axios.delete('https://ecommerce.routemisr.com/api/v1/wishlist/'+id,header)
    }
   return <WishlistContext.Provider value={{removeToWishlist,addToWishlist,getWishlist,setWishList,wishList,redHeart,setRedHeart}}>
     {children}
    </WishlistContext.Provider>
}