import axios from "axios";
import React, { useContext,useEffect, useState } from "react";
import { useQuery } from "react-query";
import Product from '../Product/Product';
import Carousel from "../Carousel/Carousel";
import { WishlistContext } from '../../Context/WishlistContext'
import CategoryCarousel from "../CategoryCarousel/CategoryCarousel";

export default function Home() {
  let[page,setPage]=useState(1)
  let{getWishlist,wishList,setWishList}=useContext(WishlistContext)
  function getProducts(qData) {
    return axios.get(`https://ecommerce.routemisr.com/api/v1/products?page=${qData.queryKey[1]}`);
  }
  let { isLoading, data } = useQuery(["getAllProducts",page], getProducts);
  function getPageNumber(e){
    setPage(e.target.getAttribute("pagenum"))
  }
  // let [loading,setLoading]=useState(false)
  // let [products,setProducts]=useState([])
  // async function getProducts(){
  //   setLoading(true)
  //   let {data}=await axios.get("https://ecommerce.routemisr.com/api/v1/products")
  //    setProducts(data.data)
  //    setLoading(false)
  // }
  // useEffect(()=>{
  //   getProducts()
  // },[])
  async function getLoggedWishlist(){
    let req= await getWishlist().catch()
    if(req.data.status=='success'){
      setWishList(req)
    }
  }
useEffect(()=>{
    getLoggedWishlist()
},[])
  return isLoading ? (
    <div className="d-flex justify-content-center align-items-center vh-100 position-fixed w-100 top-0 bottom-0 bg-light z-3">
      <span className="loader"></span>
    </div>
  ) : (
    <div className="container mt-5 pt-5">
      <Carousel/>
      <CategoryCarousel/>
      <div className="row g-3">
        {data?.data.data.map((item) => {
          return <Product key={item.id} item={item}></Product>;
        })}
       <nav aria-label="Page navigation example ">
          <ul className="pagination justify-content-center">
            <li className="page-item">
              <a className="page-link" href="#" aria-label="Previous">
                <span aria-hidden="true">&laquo;</span>
                <span className="sr-only">Previous</span>
              </a>
            </li>
            <li className="page-item">
              <a className="page-link cursor-pointer" pagenum='1' onClick={getPageNumber}>1</a>
            </li>
            <li className="page-item">
              <a className="page-link cursor-pointer" pagenum='2' onClick={getPageNumber}>2</a>
            </li>
            <li className="page-item">
              <a className="page-link" href="#" aria-label="Next">
                <span aria-hidden="true">&raquo;</span>
                <span className="sr-only">Next</span>
              </a>
            </li>
          </ul>
        </nav>
      </div>
    </div>
  );
}
