import axios from 'axios'
import React from 'react'
import { useQuery } from 'react-query'

export default function Categories() {
    function getAllCategories(){
        return axios.get("https://ecommerce.routemisr.com/api/v1/categories")
        }
        let{data,isLoading}= useQuery("getALllCategories",getAllCategories)
  return (
    <>
    {
   isLoading?<div className='d-flex justify-content-center align-items-center vh-100 position-fixed w-100 top-0 bg-light'><span className="loader"></span></div>:
    <div className='container mt-5 pt-5'>
      <div className='row g-3'>
      {data?.data.data.map((item)=>{
     return <div key={item._id} className='col-md-3'>
      <img src={item.image} className='w-100'/>
     </div>
      })}
      </div>
     </div>
} 
   </>
  )
}
