import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import { useQuery } from "react-query";
import { useParams } from "react-router-dom";
import { CartContext } from "../../Context/CartContext";
import Swal from "sweetalert2";

export default function ProductDetails() {
  let [_id, setId] = useState();
 let {addToCart,setCartCount}= useContext(CartContext)
  function getDetails(id) {
    return axios.get(
      `https://ecommerce.routemisr.com/api/v1/products/${id.queryKey[1]}`
    );
  }
  async function addCart(id){
    let req= await addToCart(id);
    if(req.data.status=='success'){
      setCartCount(req.data.numOfCartItems)
      Swal.fire({
        title: "Good job!",
        text: "You clicked the button!",
        icon: "success"
      });
    }
  }
  let param = useParams();
  useEffect(() => {
    setId(param.id);
  }, []);
  let { isLoading, data } = useQuery(["GetDetails", _id], getDetails);
  function showImage(e) {
  let img=  e.target.getAttribute("src");
  document.querySelector('#imgCover').setAttribute("src",img)

  }
  return isLoading ? (
    <div className="d-flex justify-content-center align-items-center vh-100 position-fixed w-100 top-0 bg-light">
      <span className="loader"></span>
    </div>
  ) : (
    <div className="container mt-5 py-5">
      <div className="row align-items-center">
        <div className="col-md-4">
          <div className="row g-1 justify-content-center align-items-center">
            <div className="col-md-3">
              {data?.data.data.images.map((ele, i) => {
                return (
                  <img
                    src={ele}
                    className="w-100 cursor-pointer"
                    onClick={showImage}
                    key={i}
                  />
                );
              })}
            </div>
            <div className="col-md-9">
                   
              <img src={data?.data.data.imageCover} id="imgCover" className="w-100 "  />
            </div>
          </div>
        </div>
        <div className="col-md-8">
          <h2>{data?.data.data.title}</h2>
          <p>{data?.data.data.description}</p>
          <div className="d-flex justify-content-between">
            <p>{data?.data.data.price}EGP</p>
            <p>
              {data?.data.data.ratingsQuantity}{" "}
              <i className="fa-solid fa-star rating-color"></i>
            </p>
          </div>
          <button className="btn bg-main my-2 text-light d-block w-100" onClick={()=>{
            addCart(data?.data.data.id)
          }}>
            Add to cart
          </button>
        </div>
      </div>
    </div>
  );
}
