import axios from 'axios'
import React, { useEffect } from 'react'
import { useQuery } from 'react-query'

export default function Brands() {
   function getAllBrands(){
  return axios.get("https://ecommerce.routemisr.com/api/v1/brands")
  }
  let{data,isLoading}= useQuery("getALllBrands",getAllBrands)
  return (
   <>
    {isLoading?<div className='d-flex justify-content-center align-items-center vh-100 position-fixed w-100 top-0 bg-light'><span className="loader"></span></div>:
    <div className='container mt-5 pt-5'>
      <div className='row g-3'>
      {data?.data.data.map((item)=>{
     return <div key={item._id} className='col-md-3'>
      <img src={item.image}/>
     </div>
      })}
      </div>
     </div>} 
   </>
  )
}
