import axios from "axios";
import { useFormik } from "formik";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import * as yup from "yup";

export default function ForgetPassword() {
  let nav = useNavigate();
  let [loading, setloading] = useState(false);
  let [error, setverifyError] = useState("");
  let [verifyerror, setError] = useState("");
  let [verifyPage, setVerifyPage] = useState(false);
  let validationSchema = yup.object({
    email: yup.string().required("email required").email("enter valid email"),
  });
  let validationSchemaVerify = yup.object({
    resetCode: yup
      .string()
      .required("requierd")
      .matches(/^[0-9]{5,6}$/),
  });
  async function forgotPasswords(val) {
    setloading(true);
    let data = await axios
      .post("https://ecommerce.routemisr.com/api/v1/auth/forgotPasswords", val)
      .catch((er) => {
        setError(er.response.data.message);
        setloading(false);
      });

    if (data?.data.statusMsg === "success") {
      setloading(false);
      setVerifyPage(true);
    }
  }
  async function verifyCode(val) {
    setloading(true);
    let data = await axios
      .post("https://ecommerce.routemisr.com/api/v1/auth/verifyResetCode", val)
      .catch((er) => {
        setverifyError(er.response.data.message);
        setloading(false);
      });
    if (data?.data.status === "Success") {
        setloading(false);
        nav("/resetPassord")
    }
  }
  let formk = useFormik({
    initialValues: {
      email: "",
    },
    onSubmit: (val) => {
      forgotPasswords(val);
    },
    validationSchema,
  });
  let formkVerify = useFormik({
    initialValues: {
      resetCode: "",
    },
    onSubmit: verifyCode,
    validationSchema: validationSchemaVerify,
  });
  return (
    <div className="container mt-5 pt-5">
      <h2>Forget Password</h2>
      {error ? <div className="alert alert-danger">{error}</div> : ""}
      {verifyerror?<div className="alert alert-danger">{verifyerror}</div> : ""}
      {!verifyPage ? (
        <form onSubmit={formk.handleSubmit}>
          <label htmlFor="email">email</label>
          <input
            type="email"
            onBlur={formk.handleBlur}
            onChange={formk.handleChange}
            name="email"
            id="email"
            className="form-control"
          />
          {formk.errors.email && formk.touched.email ? (
            <div className="alert alert-danger">{formk.errors.email}</div>
          ) : (
            ""
          )}
          {loading ? (
            <button className="btn btn-success mt-3">
              <i className="fa-solid fa-spinner fa-spin"></i>
            </button>
          ) : (
            <button type="submit" className="btn btn-success mt-3">
              Send Code
            </button>
          )}
        </form>
      ) : (
        <form onSubmit={formkVerify.handleSubmit}>
          <label htmlFor="resetCode">resetCode</label>
          <input
            value={formkVerify.values.resetCode}
            onBlur={formkVerify.handleBlur}
            onChange={formkVerify.handleChange}
            name="resetCode"
            id="resetCode"
            className="form-control"
          />
          {formkVerify.errors.resetCode && formkVerify.touched.resetCode ? (
            <div className="alert alert-danger">
              {formkVerify.errors.resetCode}
            </div>
          ) : (
            ""
          )}
          {loading ? (
            <button className="btn btn-success mt-3">
              <i className="fa-solid fa-spinner fa-spin"></i>
            </button>
          ) : (
            <button type="submit" className="btn btn-success mt-3">
              Send Code
            </button>
          )}
        </form>
      )}
    </div>
  );
}
