import React, { useContext, useEffect, useState } from 'react'
import { WishlistContext } from '../../Context/WishlistContext'
import Product from '../Product/Product'
import Swal from "sweetalert2";


export default function Wishlist() {
  let{getWishlist,wishList,setWishList,removeToWishlist}=useContext(WishlistContext)
  let[isLoading,setLoading]=useState(false)
  async function removeWishlist(id) {
    let req = await removeToWishlist(id).catch();
    if(req.data.status=='success'){
      setWishList(req)
      getLoggedWishlist()
      Swal.fire({
        title: "Good job!",
        text: "You clicked the button!",
        icon: "success",
      });
    }
  }
 
    async function getLoggedWishlist(){
        setLoading(true)
        let req= await getWishlist().catch()
        if(req.data.status=='success'){
            setLoading(false)
            setWishList(req)
        }
    }
    useEffect(()=>{
        getLoggedWishlist()
    },[])
  return (
  isLoading?<div className='d-flex justify-content-center align-items-center vh-100 position-fixed w-100 top-0 bg-light'><span className="loader"></span></div>:
  wishList?.data?.data.length==0? <div className='container mt-5 pt-5 vh-100 d-flex justify-content-center align-items-center'><div className="alert alert-warning">empty Wishlist</div></div>:
   <div className='container mt-5 pt-5'>
      <div className='row g-3'>
      {wishList?.data?.data.map((item)=>{
      return <Product key={item.id} item={item} removeWishlist={removeWishlist}>
      </Product>
      })}
      </div>
     </div>
  )
}
