import React, { useContext, useEffect, useState } from "react";
import { CartContext } from "../../Context/CartContext";
import { UserContext } from "../../Context/UserContext";
import { jwtDecode } from "jwt-decode";

export default function Allorders() {
  let { GetOrders } = useContext(CartContext);
  let [product, setProduct] = useState([]);
  let[isLoading,setLoading]=useState(false)
  async function getAllOrders() {
    setLoading(true)
    let token=jwtDecode(localStorage.getItem("user"))
    let req = await GetOrders(token.id).catch((e)=>{
      console.log(e);
    setLoading(false)
    });
    setProduct(req?.data);
    setLoading(false)
  }

  useEffect(() => {
    getAllOrders();
  }, []);
  return isLoading ? (
    <div className="d-flex justify-content-center align-items-center vh-100 position-fixed w-100 top-0 bottom-0 bg-light z-3">
      <span className="loader"></span>
    </div>
  ):  (
    <div className="container mt-5 pt-5">
      <div className="row">
        {product?.map((e) => {
          return (
            <div className="row p-3 shadow-lg my-2" key={e.id}>
              <h3 className="fw-bold">{e.id} is Processing</h3>
              <p>{e.totalOrderPrice} EGP</p>
              {e.cartItems.map((ele) => {
               return <div className="col-2" key={ele.product._id}>
                  <img src={ele.product.imageCover} className="w-100"/>
                </div>;
              })}
            </div>
          );
        })}
      </div>
    </div>
  );
}
