import React, { useContext, useEffect, useState } from 'react'
import { CartContext } from '../../Context/CartContext';
import { toast } from 'react-toastify';
import { Link } from 'react-router-dom';

export default function Cart() {
  let{GetCarts,cartCount,setCartCount,DeleteCartItem,UpdateCartItem}=useContext(CartContext)
  let[product,setProduct]=useState([])
  let[isLoading,setLoading]=useState(false)
  let[totalPrice,setTotalPrice]=useState(0)
  async function getCartCount(){
    setLoading(true)
    let req=await GetCarts().catch((er)=>{
    setLoading(false)
    });
    if(req?.data.status=='success'){
    setProduct(req?.data.data)
    setTotalPrice(req.data.data.totalCartPrice);
    setLoading(false)
    }
  }
  async function DeletCartitem(id){
    setLoading(true)
    let req=await DeleteCartItem(id).catch((e)=>{
      console.log(e);
    });
    if(req?.data.status=='success'){
    setProduct(req?.data.data)
    toast.success("success")
    setCartCount(req?.data.numOfCartItems)
    setTotalPrice(req.data.data.totalCartPrice);
    setLoading(false)
    }
  }
  async function UpdateItem(id,count){
    let req=await UpdateCartItem(id,count).catch();
    if(req?.data.status=='success'){
    setProduct(req?.data.data)
    toast.success("success")
    setTotalPrice(req.data.data.totalCartPrice);
    }
  }
  useEffect(()=>{
    getCartCount()
  },[])
  return isLoading ? (
    <div className="d-flex justify-content-center align-items-center vh-100 position-fixed w-100 top-0 bottom-0 bg-light z-3">
      <span className="loader"></span>
    </div>
  ): 
    <div className='container mt-5 pt-5'>
    { (cartCount == 0) ? <div className='container vh-100 d-flex justify-content-center align-items-center'><div className="alert alert-warning">empty Cart</div></div> :
   <>
   {
     product.products?.map((e)=>{
       return   <div className='row border-bottom justify-content-between p-3 align-items-center' key={e._id}>
       <div className='col-1'>
         <img src={e.product.imageCover} className='w-100'/>
       </div>
       <div className='col-md-9'>
         <p className='fw-bold'>{e.product.title}</p>
         <p>{e.price} EGP</p>
         <button className='btn btn-danger' onClick={()=>{
           DeletCartitem(e.product._id)
         }} >Delete <i className='fa-solid fa-trash'></i></button>
       </div>
       <div className='col-md-2'>
         <span className='btn btn-danger' onClick={()=>{UpdateItem(e.product._id,e.count-=1)}}>-</span>
         <span className='p-2'>{(e.count>0?e.count:DeletCartitem(e.product._id))}</span>
         <span className='btn btn-success' onClick={()=>{UpdateItem(e.product._id,e.count+=1)}}>+</span>
       </div>
     </div>
      
     })
   }
   <h2>Total Price: {totalPrice} EGP</h2>
   <Link className='btn btn-success' to={'/checkout/'+product._id}>Checkout</Link>
   </>
  
  }
    </div>
   
}
