import React, { useContext } from 'react'
import { CartContext } from '../../Context/CartContext'
import { useFormik } from 'formik';
import * as yup from 'yup'
import { useParams } from 'react-router-dom';

export default function Checkout() {
    let {CheckOut}=useContext(CartContext)
    let id=useParams()
   async function Pay(val){
    let data=await CheckOut(id.id,val).then();
     if(data.data.status=='success'){
        window.location.href=data.data.session.url
     }
    
   }
   let validationSchema=yup.object(
    {
        details:yup.string(),
        phone:yup.string().required("phone required").matches(/^01[1250][0-9]{8}$/,"enter valid phone"),
        city:yup.string().required(),
    }
   )
   let formk=useFormik({
    initialValues:{
        "details": "",
        "phone": "",
        "city": "",
        },
        validationSchema,
        onSubmit:Pay
    
   });
  return (
    <div className='container mt-5 pt-5'>
      <form onSubmit={formk.handleSubmit}>
        <input onBlur={formk.handleBlur} onChange={formk.handleChange} className='form-control my-2' type='text' placeholder='City' name='city'/>
        {(formk.errors.city&&formk.touched.city)?<div className='alert alert-danger mt-3'>{formk.errors.city}</div>:""}
        <input onBlur={formk.handleBlur} onChange={formk.handleChange} className='form-control my-2' type='tel' placeholder='Phone' name='phone'/>
        {(formk.errors.phone&&formk.touched.phone)?<div className='alert alert-danger mt-3'>{formk.errors.phone}</div>:""}
        <textarea className='form-control my-2' placeholder='Details' name='details'></textarea>
        {(formk.errors.details&&formk.touched.details)?<div className='alert alert-danger mt-3'>{formk.errors.details}</div>:""}
        <button type='submit' className='btn btn-success my-2'>Pay</button>
      </form>
    </div>
  )
}
